.App {
    text-align: center;
    background: #282c34;
    color: white;
}

.header {
    height: 50px;
    font-size: 30px;
    padding: 20px;
}

.score, .timer {
    padding-bottom: 20px;
}

.cards {
    margin: 0 auto;
    padding-bottom: 100px;
    max-width: 460px;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
}

.popup-content {
    background: #2b9595;
    border-radius: 5px;
    border: 0;
    color: white;
    max-width: 250px;
    text-align: center;
    padding: 20px;
}

.popup-content .close {
    cursor: pointer;
    position: absolute;
    right: 5px;
    top: -5px;
    font-size: 24px;
}

@media screen and (max-width: 500px) {
    .cards {
        max-width: 320px;
        grid-gap: 5px;
    }
}